@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;500&family=Outfit:wght@300;400;600&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body{
    font-family: 'Outfit', sans-serif;
}


.container-page {
    display: flex;
    flex-direction: column;
    
}

/* @media (max-width: 190px) {
    .containerGeralborder{ 
        border: solid black 2px;
    }
} */
.container {
    padding: 3% 15% 3% 15%;
    border: solid black 2px;
}

.containerGeral {
    width: 100%;
}

@media (max-width: 375px) {
    .container {
        padding: 1% 5% 1% 5%;
    }

}

@media (max-width: 260px) {
    .container {
        padding: 0.5%;

    }
}
.st0 {
  fill-rule: evenodd;
  clip-rule: evenodd;
  fill: #fcb823;
}
.st1 {
  fill: #ffffff;
}

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000000;
    height: 6rem;
    width: 100%;
}

.svg-img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 10rem;
    height: 4rem;
}

@media (max-width: 375px) {
    
    .header {
        width: 100vw;
        height: 20vh;
    }
}


/* @media (max-width: 190px) {
    .header {
        width: 150vw;
    }
} */

.title {
    font-size: 2rem;
    width: 16rem;
    height: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 1rem;
    font-weight: 600;
}

.messageOpening {
    text-align: justify;
    font-size: 1rem;
}

.spaceBlack {
    border: solid black 2px;
    width: 30%;
    height: 1rem;
    background-color: black;
    margin-top: 1rem;
    margin-bottom: 1rem;

}

.containerOpening {
    width: 100%;
}

@media (max-width: 375px) {
    
    .title {
        font-size: 2rem;
        width: 16rem;
        height: 5rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 1rem;
        font-weight: 600;
        margin-top: 3rem;
    }

    .messageOpening {
        text-align: justify;
        font-size: 1.1rem;
    }
    
    .spaceBlack {
        border: solid black 2px;
        width: 30%;
        height: 1rem;
        background-color: black;
        margin-top: 1rem;
        margin-bottom: 1rem;
    
    }
}

@media (max-width: 750px) {
    .title {
        margin-top: 1rem;
    }

}

@media (max-width: 260px) {

    .title {
        font-size: 1.7rem;
    }
}
.form {
    border: solid 1px rgba(0, 0, 0, 0.2);
    padding: 1rem;
    margin: 2rem 0 2rem 0;
    
}

.line-form {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: solid 1px rgba(0, 0, 0, 0.2);
    height: 3rem;

}

.label-info{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;


}

.periodo::first-letter {
    text-transform: capitalize;
}

.box-closeUnit {
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
    flex-wrap: wrap;
}


.selectOption {
    justify-content: space-between;
}
.form-select {
    width: 5rem;
    height: 1.8rem;
    text-align: center;
    font-size: 0.8rem;
    border: none;
    color: black;
}

.formUF {
    margin-left: 0.4rem;
}

.closeUnits, .quantitClosed {
    padding-bottom: 1rem;
}

@media (max-width: 375px) {
    
    .line-form {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: solid 1px rgba(0, 0, 0, 0.2);
        height: 3.3rem;
        font-size: 1rem;
    
    } 

    
    .box-closeUnit{
        display: flex;
        flex-direction: column;
        padding: 0.2rem;

    }

    .closeUnits {
        padding-bottom: 1rem;
    }

    .quantitClosed{
        padding-bottom: 1rem;
        padding-left: 0.3rem;
    }


}
    

.Header {
    display: flex;
    align-items: center;
}

.Header img {
    height: 1.7rem;
    margin-right: 0.8rem;
}

@media (max-width: 375px) {
    .Header {
        margin-bottom: 1rem;
    }
    
    .Header img {
        height: 1.8rem;
        margin-right: 0.8rem;
    }
    .headerHour {
        font-size: 1.3rem;
    }


}

.box-message {
    border-bottom: solid 1px rgba(0, 0, 0, 0.2);;
    height: 3rem;
    display: flex;
    align-items: center;
    font-size: 1.5rem;
}

.message::first-letter{
    text-transform: capitalize;
}



@media (max-width: 375px) {

    .box-message {
        border-bottom: solid 1px rgba(0, 0, 0, 0.2);;
        height: 3rem;
        display: flex;
        align-items: center;
        font-size: 1.4rem;
        padding-bottom: 1rem;

    }
}
.buttons {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}



.btn-serch-unit, .btn-clean {
    text-transform: uppercase;
    padding: 0.7rem 3rem 0.7rem 3rem;
    border: none;
    margin: 1rem;
    font-weight: 600;
    cursor: pointer;
    font-size: 0.7rem;

}

.btn-serch-unit{
    background-color: #FFB612;
}

.btn-clean {
    border: solid 1px rgba(0, 0, 0, 0.2) ;
}

@media (max-width: 656px) {
    .buttons {
        display: flex;
        flex-direction: column;

    }
 



}


.box-protocols{
    display: flex;
    background-color: #f1eeeef3;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}


@media (max-width: 375px) {
    .box-protocols {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-bottom: 1rem;
    }
}











.msg-protocol::first-letter  {
    text-transform: capitalize;

}
.msg-protocol {
    font-size: 0.9rem;
    margin: 1rem;
}

.box-img-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 5.5rem;

}

.box-img-info img {
    height: 4rem;
    width: 4rem;
    margin-bottom: 0.5rem;
}

@media (max-width: 375px){
    .box-img-info img {
        height: 3.3rem;
        width: 3.3rem;
        margin-bottom: 0.5rem;
    }
}

@media (max-width: 290px) {
    .box-img-info {
        width: 3.5rem;
    
    }
}
.title-protocol{
    padding: 1rem 0 0.5rem 1rem;
    font-weight: 700;

}

.title-protocol::first-letter {
    text-transform: capitalize;
}

.box-protocol{
    display: flex;
}
.protocol{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;

}
.containerBoxUniRender {
